.cards {
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: cursive;
}
.card {
  width: 350px;
  height: auto;
  background: white;
  box-shadow: 0 2px 10px #121212;
  margin: 0 20px;
  border-radius: 10px;
  padding: 20px 20px 30px;
}
.card .card-image {
  width: 100%;
  height: 50%;
}
.card-image img {
  width: 100%;
  height: 100%;
  border-radius: 10px;
}

.content{
    display: block;
    height: 50%;
    margin-bottom: 20px;
}
.content .desc{
    font-size: 12px;
    line-height: 1.6rem;
}